* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

/* Estas tarjetas se adaptan al espacio que tienen disponible */
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

/* Clase de CSS */
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  /* Para que los bordes no se salgan del contenedor */
  overflow: hidden;

}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}